<template>
  <div class='story' :style="{ 'margin-top' : getGap() + 'px' }">
    <div class='story-image' :style=" { left: left + 'px', opacity: opacity, display: display } ">
      <img :src="image[0]" @click.stop="viewImg"/>
    </div>
    <div class='story-text' ref="storyText" >
      <div class='story-text-title' v-html="title" />
      <div class='story-text-line' />
      <div class='story-text-content' v-html="text" />
    </div>
    <div class='story-shadow' ref='shadow'>
      <img src='/static/next.png' ref='next' class='story-shadow-next' @click.stop="nextImg"/>
      <img src='/static/back.png' ref='back' class='story-shadow-back' @click.stop="backImg"/>
      <img src='/static/cancel.png' ref='cancel' class='story-shadow-cancel' @click.stop="cancel"/>
      <img v-for="(img, index) in image"
        :src="img"
        :ref="'image' + (index + 1)"
        class='story-shadow-img' />
    </div>
  </div>
</template>

<script>
export default {
  name: 'Story',
  props: {
    image: {
      type: Array
    },
    title: {
      type: String
    },
    text: {
      type: String
    },
    margin: {
      type: Number
    }
  },
  mounted () {
    this.showImage(1);
  },
  computed: {
    textHeight: function() {
      return this.$refs.storyText.offsetHeight
    }
  },
  data () {
    return {
     // marginTop: this.margin + window.innerHeight,
     // gap: this.margin > 850 ? window.innerHeight : this.margin + window.innerHeight,
      left: 480,
      opacity: 0,
      display: 'none',
      curr: 1
    }
  },
  methods: {
    getGap () {
      return this.margin > 850 ? window.innerHeight : this.margin + window.innerHeight
    },
    getMarginTop () {
      return this.margin + window.innerHeight
    },
    getHeight () {
      return this.$refs.storyText.offsetHeight;
    },
    onScroll () {
      const scrolled = window.scrollY;
      const mt = this.getMarginTop();
      if (scrolled < mt) {
        this.left = 480;
        this.opacity = 0;
        this.display = 'none';
      } else if (scrolled < mt + 200) {
        this.left = 480 - (480 - 40) / 200 * (scrolled - mt);
        this.opacity = 1 / 200 * (scrolled - mt);
        this.display = 'block';
      } else if (scrolled >= mt + 200 && scrolled <= mt + this.textHeight - 200) {
        this.left = 40;
        this.opacity = 1;
        this.display = 'block';
      } else if (scrolled > mt + this.textHeight - 200 && scrolled <= mt + this.textHeight) {
        this.left = 40 - (40 + 400) / 200 * (scrolled - mt - this.textHeight + 200);
        this.opacity = 1 - 1 / 200 * (scrolled - mt - this.textHeight + 200);
        this.display = 'block';
      } else {
        this.left = -400;
        this.opacity = 0;
        this.display = 'none';
      }
    },

    showImage (number) {
      for (var i in this.$refs) {
        var node = this.$refs[i][0];
        if (node != undefined && node.tagName == 'IMG' && node.className == 'story-shadow-img') {
          if (parseInt(i.slice(5)) == number) {
            node.style.display = 'block';
          } else {
            node.style.display = 'none';
          }
          var ratio = node.naturalWidth / node.naturalHeight;
          var windowRatio = window.innerWidth / window.innerHeight;
          if (ratio < windowRatio) {
            node.style.height = window.innerHeight + 'px';
            node.style.width = window.innerHeight * ratio + 'px';
          } else {
            node.style.width = window.innerWidth + 'px';
            node.style.height = window.innerWidth / ratio + 'px';
          }
        }
      }
    },
    nextImg () {
      if (this.curr < this.image.length) {
        this.curr += 1;
        this.showImage(this.curr);
      }
    },
    backImg () {
      if (this.curr > 1) {
        this.curr -= 1;
        this.showImage(this.curr);
      }

    },
    cancel () {
      this.$refs.shadow.style.display = 'none';
    },
    viewImg () {
      this.$refs.shadow.style.display = 'block';
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.story{
   
}
.story-image {
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
}

.story-image img {
  z-index: 3;
  width: 400px;
  height: 500px;
}

.story-text {
  margin-left: 480px;
  margin-right: 120px;
  line-height: 2
}

.story-text-line {
  height: 5px;
  width: 400px;
  background-color: #FDDC22;
}

.story-text-title {
  font-size: 80px;
}

.story-text-content {
  margin-top: 40px;
}

.story-shadow {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  display: none;
}

.story-shadow-img {
  z-index: 1;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.story-shadow-back {
  z-index: 2;
  position: fixed;
  left: 20px;
  top: 50%;
  width: 40px;
  height: 40px;
  transform: translateY(-50%);
}

.story-shadow-next {
  z-index: 2;
  position: fixed;
  right: 20px;
  top: 50%;
  width: 40px;
  height: 40px;
  transform: translateY(-50%);
}

.story-shadow-cancel {
  z-index: 2;
  position: fixed;
  left: 20px;
  top: 20px;
  width: 40px;
  height: 40px;
}
</style>



