<template>
  <div class='header'>
    <div class='header-background' :style="{ height: backgroundHeight + 'px', width: backgroundWidth + 'px' }"/>
    <div class='header-shadow' :style="{ 
      left: left + 'px', 
      height: backgroundHeight + 'px', 
      width: backgroundWidth - left + 190 + 'px' 
    }"/>
    <div class='header-title' :style="{ 
      top: titleY + 'px', 
      'font-size': titleSize + 'px'
    }" v-html="title" />
    <div class='header-description' :style="{ width: backgroundWidth + 'px' }">
      <div class='header-description-line' />
      <div class='header-description-text description' v-html="description" />
    </div>
  </div>
</template>

<script>
export default {
  name: 'Header',
  props: ['title', 'description'],
  data () {
    return {
      backgroundHeight: window.innerHeight - 80,
      backgroundWidth: window.innerWidth - 220,
      left: window.innerWidth - 30,
      titleY: 100,
      titleSize: 100,
    }
  },
  methods: {
    onScroll () {
      const scrolled = window.scrollY;
      if (scrolled < (this.backgroundHeight)) {
        this.left = window.innerWidth - 30 - (window.innerWidth - 220) / (this.backgroundHeight) * scrolled;
        this.titleY = 100 - (100 - 40) / this.backgroundHeight * scrolled;
        this.titleSize = 100 - (100 - 20) / this.backgroundHeight * scrolled;
      } else {
        this.left = 190;
        this.titleY = 40;
        this.titleSize = 20;
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header {
  position: relative;
  margin-top: 40px;
}
.header-background {
  position: fixed;
  top: 40px;
  background-color: #9BD770;
  left: 190px;
  z-index: -2;
}
.header-shadow {
  position: fixed;
  top: 40px;
  width: 0;
  z-index: -1;
  background-color: #EBF7E3
}
.header-title {
  position: fixed;
  text-align: left;
  left: 40px;
  font-weight: bold;
  z-index: 0;
  color: #1B3409;
}
.header-description {
  position: fixed;
  left: 220px;
  top: 260px;
  max-width: 600px;
  z-index: -2;
}
.header-description-line {
  height: 5px;
  width: 200px;
  background-color: #FDDC22;
}
.header-description-text {
  margin-top: 30px;
  font-family: 'STHeiti';
  font-weight: bold;
  line-height: 1.5;
  color: #375F1B;
}
</style>



