<template>
  <div class='timeline' :style="{ right: right.value + 'px' }">
    <div v-for="(milestone, index) in milestones.history" 
         :id="'timeline-circle-' + milestone.time" 
         class='timeline-circle' >
        <img v-tooltip="{
          content: milestone.time + '<br/>' + milestone.text,
          placement: 'right-center',
          offset: 5
        }" :src="milestone.img" class='timeline-circle-img'/>
    </div>
    <div class='timeline-circle-line' :style="{ opacity: opacity.value, height: this.barHeightUpperLimit + 'px' }" />
    <div class='timeline-circle-line-sub' :style="{ height: this.barHeight + 'px' }" />
  </div>
</template>

<script>
export default {
  name: 'Timeline',
  props: {
    milestones: {
      type: Object
    },
    heightTest: {
      type: Array
    }
  },
  data () {
    return {
      right: {
        upper: 200,
        value: 200,
        lower: 30
      },
      opacity: {
        upper: 1,
        value: 1,
        lower: 0.2
      },
      speed: 0.2,
      barHeight: 0
    }
  },
  computed: {
    barHeightUpperLimit: function() {
      return (this.heightTest.length - 1) * 2 * 60
    },
    scrollHeight: function() {
      var res = new Array(this.heightTest.length);
      res[0] = (this.right.upper - this.right.lower) / this.speed + window.innerHeight;
      for (var i = 1; i < this.heightTest.length; ++i) {
        res[i] = res[i - 1] + this.heightTest[i - 1];
      }
      return res;
    }
  },
  methods: {
    onScroll () {
      const scrolled = window.scrollY;
      if (scrolled * this.speed < (this.right.upper - this.right.lower)) {
        this.right.value = this.right.upper - scrolled * this.speed;
        this.opacity.value = 1 - (1 - this.opacity.lower) / (this.right.upper - this.right.lower) * scrolled * this.speed;
      } else {
        this.right.value = this.right.lower;
        this.opacity.value = this.opacity.lower;
      }
      if (scrolled > (this.right.upper - this.right.lower) / this.speed + window.innerHeight) {
        var index = this.scrollHeight.length - 1;
        for (var i = 0; i < this.scrollHeight.length - 1; ++i) {
          if (scrolled >= this.scrollHeight[i] && scrolled < this.scrollHeight[i + 1]) {
            index = i;
            break;
          }
        }
        const baseHeight = 120 * index;
        const offsetHeight = (scrolled - this.scrollHeight[index]) * (120 / this.heightTest[index]);
        this.barHeight = (baseHeight + offsetHeight) <= this.barHeightUpperLimit ? (baseHeight + offsetHeight) : this.barHeightUpperLimit;
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.timeline {
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1000;
}
.timeline-circle {
  position: relative;
  height: 60px;
  width: 60px;
  background-color: #FDDC22;
  border-radius: 30px;
  color: #375F1B;
  line-height: 60px;
  text-align: center;
  margin-top: 60px;
  margin-bottom: 60px;
}
.timeline-circle-img {
  width: 54px;
  height: 54px;
  border-radius: 27px;
  margin-top: 3px;
  overflow: hidden;
  cursor: pointer;
}
.timeline-circle-line {
  position: absolute;
  top: 90px;
  left: 25px;
  width: 10px;
  background-color: #FDDC22;
  z-index: -2;
}
.timeline-circle-line-sub {
  position: absolute;
  top: 90px;
  left: 25px;
  height: 0px;
  width: 10px;
  background-color: #FDDC22;
  z-index: -1;
}
</style>



